    limpiar() {
      this._id = "";

      (this.usuario = ""),
        (this.folio = ""),
        (this.agencia = ""),
        (this.fecha = ""),
        (this.pasajeros = []),
        (this.vehiculos = []),
        (this.valida = 0),
        (this.validaMensaje = []),
        (this.editedIndex = -1);
    },

    handleFileUpload(e) {
      this.file = this.$refs.file.files[0];
      this.file2 = this.$refs.file1.files[0];
      this.file3 = this.$refs.file3.files[0];
      console.log(this.file3);
    },

    guardar() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let formData3 = new FormData();
      formData3.append("file3", this.file3, this.file3.name);
      let formData2 = new FormData();
      formData2.append("file0", this.file2, this.file2.name);
      let formData = new FormData();
      formData.append("file1", this.file, this.file.name);
      let configuracion = { headers: header };

      if (this.editedIndex > -1) {
        axios
          .put("VehiculoPersonal/addVehiculos/" + this._id, formData)
          .then(function (response) {
            me.limpiar();
            me.close();
            me.listar();
          })
          .catch(function (error) {
            console.log(error);
          });
      } else {
        axios
          .post(
            "vehiculoPersonal/add",
            {
              embarcacion: this.embarcacion,
              usuario: this.$store.state.usuario._id,
              muelle: this.muelle,
              fecha: this.fecha,
              pasajeros: this.pasajeros,
              vehiculos: this.vehiculos,
            },
            configuracion
          )
          .then(function (response) {
            axios.put("vehiculoPersonal/update/" + response.data._id, formData);
            axios.put(
              "vehiculoPersonal/updateVehiculos/" + response.data._id,
              formData2
            );
            axios.put(
              "vehiculoPersonal/updatePersonal/" + response.data._id,
              formData3
            );
            me.limpiar();
            me.ocultarNuevo();
            me.close();
            me.listar();
          })
          .catch(function (error) {
            console.log(error);
          });
      }
    },
