                      "La solicitud unica ha sido actualizada exitosamente",
                      "success"
                    )
                    me.close();
                    me.ocultarNuevo();
                    me.listar();
               }).catch(function(error){
                  swal(
                      "Error",
                      "La solicitud unica no se ha actualizado favor de llenar los campos correctamente",
                      "warning"
                    )
                   
               });
              }else{
                  if(this.operacion_carga=='' && this.operacion_descarga==''){
                    this.operacion_carga=false,
                    this.operacion_descarga=false
                  }
                  if(this.operacion_carga==''){
                    this.operacion_carga=false
                  }
                  if(this.operacion_descarga==''){
                    this.operacion_descarga=false
                  }
                  this.submited = true
                  this.$v.$touch();
                  if(this.$v.$invalid){
                    swal(
                      "Error",
                      "Verificar los datos ingresados",
                      "error"
                    )
                    return false
                  }
                axios.post('solicitudUnica/add',
                {
                  
                    'embarcacion':this.embarcacion,
                    'usuario':this.$store.state.usuario._id,            
                    'detalles':this.detalles,
