            this.muelle = item.muelle
            this.embarcacion = item.embarcacion._id
            this.infraestructura = item.infraestructura
            this.servicios_port=item.servicios_portuarios
            this.otros_servicios =item.otros_servicios

            this.operacion_carga=item.operacion_carga
            this.operacion_descarga=item.operacion_descarga
            this.observaciones=item.observaciones
            this.tipo_carga=item.tipo_carga
            this.total_toneladas=item.total_toneladas
            this.listarDetalles(item._id);
            this.capitan=item.capitan
            this.verNuevo=1;
            this._id=item._id;
            this.no_tripulantes=item.no_tripulantes
            this.editedIndex=1;
            this.dialog = false
            this.verNuevo=1
          },

        activarDesactivarMostrar(accion,item){
          this.adModal=1;
          this.adNombre=item.folio_solicitud;
          this.adId=item._id;
          if(accion==1){
            this.adAccion=1
          }else if(accion==2){
            this.adAccion=2
          }else{
            this.adMmodal=0;
          }
        },
        activar(){
          let me=this;
           let header={"Token":this.$store.state.token};
           let configuracion= {headers: header}
           axios.put('usuario/activate',{'_id':this.adId},configuracion)
               .then(function(response){
                 me.adModal=0,
                 me.adAccion=0,
                 me.adNombre='',
                 me.adId='',
                 me.listar();
               }).catch(function(error){
                 console.log(error)
               });
        },

        desactivar(){
          let me=this;
           let header={"Token":this.$store.state.token};
           let configuracion= {headers: header}
           axios.put('usuario/deactivate',{'_id':this.adId},configuracion)
               .then(function(response){
                 me.adModal=0,
                 me.adAccion=0,
                 me.adNombre='',
                 me.adId='',
                 me.listar();
               }).catch(function(error){
                 console.log(error)
               });
        },

          
        activarDesactivarCerrar(){
          this.adModal=0;
        },

          close () {
            this.dialog = false
          },

         
        }
  }
</script>
