            this.valida=0;
            this.validaMensaje=[];
            if(!this.rol){
              this.validaMensaje.push('El rol es obligatorio');
            }
            if(this.empresa.length<1 || this.empresa.length>30){
              this.validaMensaje.push('El nombre de la empresa debe tener entre 1 y 30 caracteres');
            }
            if(this.nombre.length<1 || this.nombre.length>50){
              this.validaMensaje.push('El nombre del usuario debe tener entre 1 y 50 caracteres');
            }
             if(this.email.length<1 || this.email.length>50){
              this.validaMensaje.push('El email debe tener entre 1 y 50 caracteres');
            }
             if(this.password.length<1 || this.password.length>64){
              this.validaMensaje.push('El password debe tener entre 1 y 64 pasajeros');
            }
           

           
           
            if(this.validaMensaje.length){
              this.valida=1;
            }
            return this.valida;
          },

          agregarDetalle(){
            this.pasajeros.push(
              {
                nombreCompleto: '',
                identificacion: '',
                libretaMar: '',
                destino:''
              }
            )
          },
          limpiar(){
            this._id=''
           
            this.usuario='',
            this.folio='',
            this.agencia='',
            this.fecha='',

            this.pasajeros=[],
          
            this.valida=0,
            this.validaMensaje=[],
            this.editedIndex=-1;
          },
          guardar(){
            let me=this;
            let header={"Token":this.$store.state.token};
            let configuracion= {headers: header}
              if(this.validar()){
              return;
            }
           
              if(this.editedIndex >-1 ){
                axios.put('solicitudUnica/update',
                {
                    '_id':this._id,
                    'embarcacion':this.embarcacion,
                    'usuario':this.$store.state.usuario._id,            
                    'detalles':this.detalles,
                    'agencia':this.agencia,
                    'eta':this.eta.toLocaleString(),
                    'etd':this.etd.toLocaleString(),
                    'comentarios':this.comentarios,
                    'procedencia':this.procedencia
                
                    
               },configuracion)
               .then(function(response){
                    this.verNuevo =0
                    me.close();
                    me.ocultarNuevo();
                    me.listar();
               }).catch(function(error){
                 console.log(error)
               });
              }else{
                console.log(this.eta)
                axios.post('solicitudUnica/add',
                {
                  
                    'embarcacion':this.embarcacion,
                    'usuario':this.$store.state.usuario._id,            
                    'detalles':this.detalles,
                    'agencia':this.agencia,
                    'eta':this.eta.toLocaleString(),
                    'etd':this.etd.toLocaleString(),
                    'comentarios':this.comentarios,
                    'procedencia':this.procedencia
                    
                    
               },configuracion)
               .then(function(response){
                 me.limpiar();
                 me.close();
                 me.listar();
               }).catch(function(error){
                 console.log(error)
               });
              }
             
          },
          listar(){
            let header={"Token":this.$store.state.token};
            let configuracion= {headers: header}
            let me=this;
            axios.get('solicitudUnica/listAdmin',configuracion).then(function (response){
            me.solicitudUnica=response.data;
            console.log(me.solicitudUnica)
            }).catch(function(error){
              console.log(error)
            })
          },
        
          listarDetalles(id){
            let me=this;
            let header={"Token":this.$store.state.token};
            let configuracion= {headers: header}
            axios.get('solicitudUnica/query?_id='+id,configuracion).then(function (response){
           
            console.log(configuracion)
            }).catch(function(error){
              console.log(error)
            })
          },

           listarEmbarcacion(embarcacion,procedencia,usuario,detalles,eta,etd,muelle,estado,cancelacion,correo){
           
            let header={"Token":this.$store.state.token};
            let me=this;
            let embarcacio
            
            let configuracion= {headers: header}
            axios.get('embarcacion/query?_id='+embarcacion,configuracion).then(function (response){
            embarcacio=response.data;
            console.log(estado)
            let status="aprobada"
              me.enviar(embarcacio,procedencia,usuario,detalles,eta,etd,muelle,status,cancelacion,correo)
            }).catch(function(error){
              console.log(error)
            })
          },

          imprimir(){
            var objeto = document.getElementById("solicitudGuardia"); //obtenemos el objeto a imprimir
      var ventana = window.open("", "_blank"); //abrimos una ventana vacía nueva
      ventana.document.write(objeto.innerHTML); //imprimimos el HTML del objeto en la nueva ventana
      ventana.document.close(); //cerramos el documento
      ventana.print(); //imprimimos la ventana
      ventana.close(); //cerramos la ventana
          },

            listarEmbarcacionCancelada(embarcacion,procedencia,usuario,detalles,eta,etd,muelle,estado,cancelacion,correo){
           
            let header={"Token":this.$store.state.token};
            let me=this;
            let embarcacio
            
            let configuracion= {headers: header}
            axios.get('embarcacion/query?_id='+embarcacion,configuracion).then(function (response){
            embarcacio=response.data;
            console.log(estado)
            let status="cancelada"
              me.enviar(embarcacio,procedencia,usuario,detalles,eta,etd,muelle,status,cancelacion,correo)
            }).catch(function(error){
              console.log(error)
            })
          },
          verGuardia(item){
            this.limpiar();
            this.fecha = item.fecha
            this.folio = item.folio
            this.detalles = item.detalles
            this.agencia = item.agencia._id
            this.embarcacion = item.embarcacion._id
            this.listarDetalles(item._id);
            this.verNuevo=1;
          },

          editItem (item) {
            this.limpiar();
            this.eta = item.eta
            this.etd = item.etd
            this.procedencia = item.procedencia
            this.comentarios = item.comentarios
            this.agencia = item.agencia._id
            this.embarcacion = item.embarcacion._id
            this.listarDetalles(item._id);
            this.verNuevo=1;
            this._id=item._id;
           
            this.editedIndex=1;
            this.dialog = false
            this.verNuevo=1
          },

        activarDesactivarMostrar(accion,item,folio){
          this.adModal=1;
          this.adNombre=folio.folioId;
          this.folio = folio
          if(accion==1){
            this.adAccion=1
          }else if(accion==2){
            this.adAccion=2
            
          }else if(accion==3){
            this.adAccion=3
          }
          else if(accion==4){
            this.adAccion=4
          }
           else if(accion==5){
            this.adAccion=5
          }
          else{
            this.adMmodal=0;
          }
        },
        activar(){
          let me=this;
           let header={"Token":this.$store.state.token};
            var usuario=''
           let configuracion= {headers: header}
           axios.put('solicitudUnica/activate',{'_id':this._id,'aprobacionAmls':this.$store.state.usuario.nombre},configuracion)
          
               .then(function(response){
                
                me.listarEmbarcacion(response.data.embarcacion, response.data.procedencia,response.data.usuario.nombre,response.data.comentarios,moment(response.data.eta).format('MMMM Do YYYY, h:mm a'),moment(response.data.etd).format('MMMM Do YYYY, h:mm a'),response.data.muelle,status)
                swal(
                  "Solicitud Confirmada",
                  "Solicitud confirmada con exito, en espera de aprobación de APITAB",
                  "success"
                )
                me.ocultarComprobante()
                 me.adModal=0,
                 me.adAccion=0,
                 me.adNombre='',
                 me.adId='',
                 me.listar();
               }).catch(function(error){
                 console.log(error)
               });
        },

         finalizarServicio(){
          let me=this;
           let header={"Token":this.$store.state.token};
            var usuario=''
           let configuracion= {headers: header}
           axios.put('solicitudUnica/finalizarServicio',{'_id':this._id,'aprobacionAmls':this.$store.state.usuario.nombre},configuracion)
          
               .then(function(response){
                
                swal(
                  "Solicitud Finalizada",
                  "La solicitud se ha guardado como realizada",
                  "success"
                )
                me.ocultarComprobante()
                 me.adModal=0,
                 me.adAccion=0,
                 me.adNombre='',
                 me.adId='',
                 me.listar();
               }).catch(function(error){
                 console.log(error)
               });
        },

          finalizarServicioNoRealizado(){
          let me=this;
           let header={"Token":this.$store.state.token};
            var usuario=''
           let configuracion= {headers: header}
           axios.put('solicitudUnica/finalizarServicioNoRealizado',{'_id':this._id,'aprobacionAmls':this.$store.state.usuario.nombre},configuracion)
          
               .then(function(response){
                
                swal(
                  "Solicitud Finalizada",
                  "La solicitud se ha guardado como no realizada",
                  "success"
                )
                me.ocultarComprobante()
                 me.adModal=0,
                 me.adAccion=0,
                 me.adNombre='',
                 me.adId='',
                 me.listar();
               }).catch(function(error){
                 console.log(error)
               });
        },

         aprobarApi(item){
          let me=this;
          let cancelacion=''
           let header={"Token":this.$store.state.token};
           let configuracion= {headers: header}
           axios.put('solicitudUnica/aprobarApi',{'_id':this._id,'aprobacionApi':this.$store.state.usuario.nombre},configuracion)
               .then(function(response){
                 me.listarEmbarcacion(response.data.embarcacion, response.data.procedencia,response.data.usuario.nombre,response.data.comentarios,moment(response.data.eta).format('MMMM Do YYYY, h:mm a'),moment(response.data.etd).format('MMMM Do YYYY, h:mm a'),response.data.muelle,status,cancelacion,response.data.usuario.email)
                 me.ocultarComprobante()
                  swal(
                  "Solicitud Confirmada",
                  "Solicitud confirmada con exito por ausencia de APITAB",
                  "success"
                )
                 me.adModal=0,
                 me.adAccion=0,
                 me.adNombre='',
                 me.adId='',
                 me.listar();
               }).catch(function(error){
                 console.log(error)
               });
        },
        

        desactivar(){
          let me=this;
           let header={"Token":this.$store.state.token};
           let configuracion= {headers: header}
           axios.put('solicitudUnica/deactivate',{'_id':this._id,'aprobacionAmls':this.$store.state.usuario.nombre,'motivo_cancelacion':this.motivo_cancelacion},configuracion)
               .then(function(response){
                 me.listarEmbarcacionCancelada(response.data.embarcacion, response.data.procedencia,response.data.usuario.nombre,response.data.comentarios,moment(response.data.eta).format('MMMM Do YYYY, h:mm a'),moment(response.data.etd).format('MMMM Do YYYY, h:mm a'),response.data.muelle,status,me.motivo_cancelacion,response.data.usuario.email)
                  swal(
                  "Solicitud Cancelada",
                  "Solicitud Cancelada con exito",
                  "success"
                )
                me.ocultarComprobante()
                 me.adModal=0,
                 me.adAccion=0,
                 me.adNombre='',
                 me.adId='',
                 me.listar();
               }).catch(function(error){
                 console.log(error)
               });
        },
        activarDesactivarCerrar(){
          this.motivo_cancelacion=''
          this.adModal=0;
        },

          close () {
            this.dialog = false
          },

         
        }
  }
</script>

