<script>

import axios from 'axios';
import DatetimePicker from 'vuetify-datetime-picker'
import jsPDF from 'jspdf'
import html2canvas from 'html2canvas'

  export default {
     icons: {
    iconfont: 'fa4',
  },
    data(){
      return{
                  dialog: false,
                  search: '',
                 cambiosGuardia:[],
             
            editedIndex: -1,
            _id:'',
            embarcacion:'',
            embarcaciones:[],
            muelles:['FRONTERA','CHILTEPEC','SANCHEZ MAGALLANES'],
            muelle:'',
            nombreCompleto:'',
            agencias:[],
             date: new Date(),
      menu1: false,
      menu2: false,
      modal: false,
      menu2: false,
            nombre:'',
            agencia2:'',
            
            tipoServicio:'',
            agenciasUsuario:[],
            cabeceraDetalles:[
              { text: 'Borrar', value: 'borrar', sortable: false},
              { text: 'Muelle', value: 'muelle' },
           
           
            ],
            pasajeros:[],
            verNuevo:0,
            usuario:'',
            fecha_solicitud:'',
            agencia:'',
            fecha:'',
            folio:'',
            fecha:'',
            date1:'',
            date2:'',
            estado:'',
            detalles:'',
            valida:0,
            fecha:null,
            validaMensaje:[],
            aprobacionAmls:'',
            aprobacionApi:'',
            adModal:0,
            comprobanteModal:0,
            adAccion:0,
            adNombre:'',
            adjunto:'',
            adId:''
      }
    },
          computed: {
          formTitle () {
            return this.editedIndex === -1 ? 'Nueva Solicitud' : 'Editar Solicitud'
          }
        },

        watch: {
          dialog (val) {
            val || this.close()
          }
        },

        created () {
          this.selectEmbarcaciones();
        },

        methods: {

          crearPDF(){
            var quotes = document.getElementById('solicitudGuardia')
             html2canvas(quotes).then(function (canvas) {
                    var imgData = canvas.toDataURL('image/png');
                    var imgWidth = 210;
                    var pageHeight = 295;

                    var imgHeight = canvas.height * imgWidth / canvas.width;
                    var heightLeft = imgHeight;
                    
                    var doc = new jsPDF('p', 'mm', 'a4');
                    var position = 0;

                    doc.addImage(imgData, 'PNG', 0, position, imgWidth, imgHeight);
                    heightLeft -= pageHeight;

                    while (heightLeft >= 0) {
                        position = heightLeft - imgHeight;
                        doc.addPage();
                        doc.addImage(imgData, 'PNG', 0, position, imgWidth, imgHeight);
                        heightLeft -= pageHeight;
                    }
                    doc.save('solicitudCG.pdf');
            })
          },

          mostrarComprobante(){
            this.limpiar();
            this.listarEmbarcacionReporte()
            this.comprobanteModal=1     
          },
           ocultarComprobante(item){
            this.comprobanteModal=0
          },

          mostrarNuevo(){
            this.verNuevo =1;

          },
          ocultarNuevo(){
            this.verNuevo =0
            this.limpiar()
          },

          eliminarPasajero(arr,item){ 
            let i=arr.indexOf(item);
            if(i!= -1){
              arr.splice(i,1);
            }
          },

        listarEmbarcacionReporte(){
            let me=this;
            let header={"Token":this.$store.state.token};
            let configuracion= {headers: header}
            console.log(this.date1)
            console.log(this.date2)
            console.log(this.embarcacion)
            axios.get('reportes/listEmbarcacionCG?fecha1='+this.date2+'&fecha2='+this.date1+'&embarcacion='+this.embarcacion,configuracion).then(function (response){
            me.agenciasUsuario=response.data;
            }).catch(function(error){
              console.log(error)
            })
          },

          selectEmbarcaciones(){
            let me = this;
            let embarcacionArray=[];
            let header={"Token":this.$store.state.token};
            let configuracion= {headers: header}
            axios.get('embarcacion/list',configuracion).then((response)=>{
              embarcacionArray=response.data;
              embarcacionArray.map(function(x){
                me.embarcaciones.push({text:x.nombre, value:x._id})
              });
            }).catch(function(error){
              console.log(error);
            })
          },

          validar(){
            this.valida=0;
            this.validaMensaje=[];
            if(!this.embarcacion){
              this.validaMensaje.push('La embarcación es obligatorio');
            }
         
            if(!this.muelle){
              this.validaMensaje.push('El muelle es obligatoria');
            }
            
                   
            if(this.validaMensaje.length){
              this.valida=1;
            }
            return this.valida;
          },

          agregarDetalle(){
            this.pasajeros.push(
              {
                nombreCompleto: '',
                identificacion: '',
                nss: '',
                destino:'',
                adjunto:''
              }
            )
          },
          limpiar(){
            this._id=''
           
            this.usuario='',
            this.folio='',
            this.agencia='',
            this.fecha='',

            this.pasajeros=[],
          
            this.valida=0,
            this.validaMensaje=[],
            this.editedIndex=-1;
          },
    
         
        
    
          verGuardia(item){
            this.limpiar();
            this.fecha = item.fecha
            this.folio = item.folio
            this.muelle = item.muelle
            this.detalles = item.detalles
            this.agencia = item.agencia._id
            this.embarcacion = item.embarcacion._id
            this.listarDetalles(item._id);
            this.verNuevo=1;
          },


        activarDesactivarCerrar(){
          this.adModal=0;
        },

          close () {
            this.dialog = false
          },

         
        }
  }
</script>

