               { text: 'Fecha', value: 'fecha', sortable:true},
               { text: 'Embarcacion', value: 'embarcacion.nombre',sortable:false},
               { text: 'Folio', value: 'folio', sortable:false},
               { text: 'Manifiesto', value: 'manifiesto',sortable:false}, 
               { text: 'Adjuntos del Personal', value: 'personal_adj',sortable:false},
               { text: 'Adjuntos del Vehiculo', value: 'vehiculos_adj',sortable:false},  
                
            ],
            editedIndex: -1,
            _id:'',
            embarcacion:'',
            embarcaciones:[],
            muelles:['FRONTERA','CHILTEPEC','SANCHEZ MAGALLANES'],
            muelle:'',
            nombreCompleto:'',
            cargo:'',
            agencias:[],
            nombre:'',
            counter:'',
            tipo:'',
            tipo:'',
            compania:'',
            razon_social:'',
            retiro_mat:'',
            ingreso_mat:'',

            file3:'',
            agencia2:'',
            tipos:['Ayudante','Chofer'],
            tipoServicio:'',
            agenciasUsuario:[],
            cabeceraDetalles:[
              { text: 'Borrar', value: 'borrar', sortable: false},
              { text: 'Nombre Completo', value: 'nombreCompleto' },
       
              { text: 'Tipo', value: 'tipo' },
           
            ],
            cabeceraVehiculos:[
              { text: 'Borrar', value: 'borrar', sortable: false},
              { text: 'Placa', value: 'placa' },
              { text: 'Vigencia de Seguro', value: 'vigenciaSeguro' },
              { text: 'Color', value: 'color' },
              { text: 'Tipo', value: 'tipo' },
           
           
            ],

            adjuntos:[
            
              { text: 'Tarjeta de Circulacion', value: 'tarjetaCirculacion' },
              { text: 'Poliza', value: 'poliza' },
           
            ],
            pasajeros:[],
            vehiculos:[],
            verNuevo:0,
            usuario:'',
            fecha_solicitud:'',
            agencia:'',
            fecha:'',
            folio:'',
            fecha:'',
            estado:'',
            placa: '',
            prueba:[],
            file:'',
            vigenciaSeguro: '',
            color: '',
            tipoVehiculo:'',
            file2:'',
            targetaCirculacion:'',
            motivo_cancelacion:'',
            poliza:'',
            detalles:'',
            valida:0,
            fecha:null,
            validaMensaje:[],
            aprobacionAmls:'',
            aprobacionApi:'',
            adModal:0,
            comprobanteModal:0,
            adAccion:0,
            adNombre:'',
            adId:''
      }
    },
          computed: {
          formTitle () {
            return this.editedIndex === -1 ? 'Nueva Solicitud' : 'Editar Solicitud'
          }
        },

        watch: {
          dialog (val) {
            val || this.close()
          }
        },

        created () {
          this.listar();
          this.selectEmbarcaciones();
          this.selectAgencias();
          this.emailIn();

        },


        methods: {

           emailIn(){
      emailjs.init("user_QA8qMBuyHCQIHTgR0AsPx");
     },

       enviar(fecha,embarcacion,muelle,detalles,usuario,status,cancelacion,correo){
                 

                        var template_params = {
                          "fecha": fecha,
                          "embarcacion": embarcacion,
                          "muelle": muelle,
                          "detalles" : detalles,
                          "usuario": usuario,
                          "status": status,
                          "cancelacion":cancelacion,
                          "correo":correo
                       
                        }
                        
                       emailjs.send("gmail","vehiculopersonal", template_params)
                        .then(function(response) {
                            if(response.text === 'OK'){
                              
                              
                            }
                           console.log("SUCCESS. status=%d, text=%s", response.status, response.text);
                        }, function(err) {
                            alert('Ocurrió un problema al enviar el correo');
                           console.log("FAILED. error=", err);
                        });
                
           },

          crearPDF(){
            var quotes = document.getElementById('solicitudGuardia')
             html2canvas(quotes).then(function (canvas) {
                    var imgData = canvas.toDataURL('image/png');
                    var imgWidth = 200;
                    var pageHeight = 305;

                    var imgHeight = canvas.height * imgWidth / canvas.width;
                    var heightLeft = imgHeight;
                    
                    var doc = new jsPDF('p', 'mm', 'a4');
                    var position = 0;

                    doc.addImage(imgData, 'PNG', 0, position, imgWidth, imgHeight);
                    heightLeft -= pageHeight;

                    while (heightLeft >= 0) {
                        position = heightLeft - imgHeight;
                        doc.addPage();
                        doc.addImage(imgData, 'PNG', 0, position, imgWidth, imgHeight);
                        heightLeft -= pageHeight;
                    }
                    doc.save('solicitudCG.pdf');
            })
          },

          mostrarComprobante(item){
            this.limpiar();
            this.fecha = item.fecha
            this.counter = item.counter
            this.aprobacionAmls = item.aprobacionAmls
            this.detalles = item.detalles
            this.cargo = item.usuario[0].cargo
            this.nombreCompleto = item.usuario[0].nombre
            this.nombreCompleto = item.usuario
            this.aprobacionApi = item.aprobacionApi
            this.usuario = item.usuario
            this.estado = item.estado
            this.muelle = item.muelle
           this.listarAgencia(item.usuario[0].agencia)
            var solic_fec = item.fecha;
            var fecha = new Date();
            var options = { year: 'numeric', month: 'long', day: 'numeric' };
            var fec = new Date(solic_fec);   

              var vigencia = new Date(item.fecha)
            this.compania = item.compania
            this.fechaActual = new Date();
            this.fechaFinal = vigencia
            this.ingreso_mat = item.ingreso_mat
            this.retiro_mat = item.retiro_mat
            this._id = item._id
             vigencia.setDate(vigencia.getDate()+3)
            this.vigencia = vigencia
            this.tipo  = item.tipo
            console.log(this.tipo)
            this.fecha_solicitud = item.fecha.join(" al ")
            this.fecha = fecha.toLocaleDateString("es-ES", options);
            this.embarcacion = item.embarcacion
            this.listarDetalles(item._id);
            this.listarDetallesVehiculos(item._id)
            this.comprobanteModal=1   
            
             var fechaAprobacion = item.fechaAprobacion
            var fechaActual = new Date()
            var fechaAprobacionDate = new Date(fechaAprobacion);
            var minutoSumar = 5;

            fechaAprobacionDate.setMinutes(fechaAprobacionDate.getMinutes() + minutoSumar);

            if(fechaActual>=fechaAprobacionDate){
                this.bandera = true
               
            }else{
              this.bandera = false
              
            }

            
          },
           ocultarComprobante(item){
            this.comprobanteModal=0
          },

          mostrarNuevo(){
