          listarEmbarcacion(
      fecha,
      embar,
      muelle,
      detalles,
      usuario,
      correo
    ) {
      let header = { Token: this.$store.state.token };
      let me = this;
      let embarcacion;

      let configuracion = { headers: header };
      axios
        .get("embarcacion/query?_id=" + embar, configuracion)
        .then(function (response) {
          embarcacion = response.data;
          var fechaLocal;
          status = "aprobada";
          let cancelacion = "";

          me.enviar(
            fecha,
            embarcacion,
            muelle,
            detalles,
            usuario,
            status,
            cancelacion,
            correo
          );
        })
        .catch(function (error) {
          console.log(error);
        });
    },

             listarEmbarcacionCancelada(fecha,embar,muelle,detalles,usuario,cancelacion,correo){
            let header={"Token":this.$store.state.token};
            let me=this;
            let embarcacion
            
            let configuracion= {headers: header}
            axios.get('embarcacion/query?_id='+embar,configuracion).then(function (response){
            embarcacion=response.data;
            var fechaLocal
              status = 'cancelada'
           
           
            me.enviar(fecha,embarcacion,muelle,detalles,usuario,status,cancelacion,correo)
            }).catch(function(error){
              console.log(error)
            })
          },
        activarDesactivarCerrar(){
          this.adModal=0;
        },

          close () {
            this.dialog = false
          },

         
        }
