                            <th scope="col">RFC</th>
                            <th scope="col">Libreta de Mar</th>
                            <th scope="col">NSS</th>
                            <th scope="col">Destino</th>
                          </tr>
                        </thead>
                        <tbody>
                          <tr v-for="(det, index) in pasajeros" :key="det._id">
