                    <v-btn
                      @click="activarDesactivarMostrar(3,{_id},{folio})"
                      class="mx-2"
                      fab
                      dark
                      small
                      color="success"
                    >
                      <v-icon dark>check</v-icon>
                    </v-btn>
                  </div>
