            <v-autocomplete
              v-model="embarcacion"
              :items="embarcaciones"
              disabled
              label="Embarcaciones"
            ></v-autocomplete>
          </v-flex>

          <v-flex xs12 sm4 lg8 x14>
            <v-textarea v-model="detalles" disabled clearable="clearable" label="Detalles"></v-textarea>
          </v-flex>

