                  <td class="text-xs-center">
                    <v-text-field disabled v-model="item.identificacion"></v-text-field>
                  </td>
                </template>
                <template v-slot:item.libretaMar="{ item }">
                  <td class="text-xs-center">
                    <v-text-field disabled v-model="item.nss"></v-text-field>
                  </td>
                </template>
                <template v-slot:item.destino="{ item }">
                  <td class="text-xs-center">
                    <v-text-field disabled v-model="item.destino"></v-text-field>
                  </td>
                </template>
              </v-data-table>
            </template>
          </v-flex>
          <v-flex xs12 sm2 md12 lg12 xl12>
            <v-btn
              class="ma-2"
              v-if="verDetallee==0"
              @click.native="guardar()"
              outlined
              color="indigo"
            >Guardar</v-btn>
            <v-btn class="ma-2" @click.native="ocultarNuevo()" outlined color="red">Cancel</v-btn>
          </v-flex>
        </v-layout>
      </v-container>
    </v-flex>
  </v-layout>
</template>
<script>
import axios from "axios";
import VueQrcode from "@chenfengyuan/vue-qrcode";
import "font-awesome/css/font-awesome.min.css"; // Ensure you are using css-loader
import html2canvas from "html2canvas";
import jsPDF from "jspdf";
import emailjs from "emailjs-com";
import moment from "moment";
import swal from "sweetalert";

export default {
  icons: {
    iconfont: "fa4",
  },
  data() {
    return {
      dialog: false,
      search: "",
      cambiosGuardia: [],
      headers: [
        { text: "Actions", value: "action", sortable: false },
        { text: "Usuario", value: "usuario[0].nombre", sortable: true },
        { text: "Folio", value: "folio", sortable: true },
        { text: "Fecha", value: "fecha", sortable: false },
        { text: "Embarcacion", value: "embarcacion.nombre", sortable: false },
        { text: "Estatus", value: "estado", sortable: true },
        { text: "Lista de Pasajeros", value: "list_p", sortable: false },
        { text: "Adjunto", value: "adjunto" },
      ],
      editedIndex: -1,
      _id: "",
      embarcacion: "",
      embarcaciones: [],

      nombreCompleto: "",
      agencias: [],
      nombre: "",
      agenciasUsuario: [],
      agencia2: "",
      cabeceraDetalles: [
        { text: "Nombre Completo", value: "nombreCompleto" },
        { text: "Identificacion", value: "identificacion" },
        { text: "NSS", value: "libretaMar" },
        { text: "Destino/Procedencia", value: "destino" },
      ],
      pasajeros: [],
      verNuevo: 0,
      verDetallee: 0,
      agenciasUsuario: [],
      folio: "",
      fecha: "",
      fecha_solicitud: "",
      bandera: "",
      muelle: "",
      motivo_cancelacion: "",
      dialogs: "",
      nombre: "",
      cargo:'',
      folioId: "",
      retiro_mat: "",
      razon_social: "",
      correo: "",
      operacion: "",
      compania: "",
      tipo: "",
      comprobanteModalPasajero: "",
      ingreso_mat: "",
      usuario: "",
      estado: "",
      detalles: "",
      valida: 0,
      tipoServicio: "",
      validaMensaje: [],
      aprobacionAmls: "",
      aprobacionApi: "",
      barcodeValue: "test",

      adModal: 0,
      comprobanteModal: 0,
      adAccion: 0,
      adNombre: "",
      adId: "",
    };
  },
  computed: {
    formTitle() {
      return this.editedIndex === -1
        ? "Nueva Embarcacion"
        : "Editar Embarcacion";
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
  },

  created() {
    this.listar();
    this.selectEmbarcaciones();
    this.selectAgencias();
    this.emailIn();
  },

  methods: {
    crearPDF() {
      var quotes = document.getElementById("solicitudGuardia");
      html2canvas(quotes).then(function (canvas) {
        var imgData = canvas.toDataURL("image/png");
        var imgWidth = 200;
        var pageHeight = 305;

        var imgHeight = (canvas.height * imgWidth) / canvas.width;
        var heightLeft = imgHeight;

        var doc = new jsPDF("p", "mm", "a4");
        var position = 0;

        doc.addImage(imgData, "PNG", 0, position, imgWidth, imgHeight);
        heightLeft -= pageHeight;

        while (heightLeft >= 0) {
          position = heightLeft - imgHeight;
          doc.addPage();
          doc.addImage(imgData, "PNG", 0, position, imgWidth, imgHeight);
          heightLeft -= pageHeight;
        }
        doc.save("CG.pdf");
      });
    },

    enviar(
      fecha,
      embarcacion,
      muelle,
      detalles,
      operacion,
      usuario,
      status,
      cancelacion,
      correo
    ) {
      var template_params = {
        fecha: fecha,
        embarcacion: embarcacion,
        muelle: muelle,
        detalles: detalles,
        operacion: operacion,
        usuario: usuario,
        status: status,
        cancelacion: cancelacion,
        correo: correo,
      };

      emailjs.send("gmail", "cambioguardia", template_params).then(
        function (response) {
          if (response.text === "OK") {
          }
          console.log(
            "SUCCESS. status=%d, text=%s",
            response.status,
            response.text
          );
        },
        function (err) {
          alert("Ocurrió un problema al enviar el correo");
          console.log("FAILED. error=", err);
        }
      );
    },

    mostrarNuevo() {
      this.verNuevo = 1;
    },
    ocultarNuevo() {
      this.verNuevo = 0;
      this.limpiar();
    },

    eliminarPasajero(arr, item) {
      let i = arr.indexOf(item);
      if (i != -1) {
        arr.splice(i, 1);
      }
    },

    emailIn() {
      emailjs.init("user_QA8qMBuyHCQIHTgR0AsPx");
    },

    mostrarComprobante(item) {
      this.limpiar();
      this.folio = item.folio;
      this._id = item._id;
      this.aprobacionAmls = item.aprobacionAmls;
      this.detalles = item.detalles;
      this.aprobacionApi = item.aprobacionApi;
      this.usuario = item.usuario;
      this.muelle = item.muelle;
      this.operacion = item.operacion;
      this.compania = item.compania;
      if (item.tipo == "Contratista") {
        this.tipo = "Contratista";
      } else {
        this.tipo = "Armador";
      }

      var fechaAprobacion = item.fechaAprobacion;
      var fechaActual = new Date();
      var fechaAprobacionDate = new Date(fechaAprobacion);
      var minutoSumar = 5;

      fechaAprobacionDate.setMinutes(
        fechaAprobacionDate.getMinutes() + minutoSumar
      );

      if (fechaActual >= fechaAprobacionDate) {
        this.bandera = true;
      } else {
        this.bandera = false;
      }

      this.ingreso_mat = item.ingreso_mat;
      this.retiro_mat = item.retiro_mat;
      this.estado = item.estado;
      this.compania = item.compania;

      this.fecha_solicitud = item.fecha.join(" al ");

      this.listarAgencia(item.usuario[0].agencia);
      this.nombre = item.usuario[0].nombre;
      this.cargo = item.usuario[0].cargo
      var solic_fec = item.fecha;
      var fecha = new Date();
      var options = { year: "numeric", month: "long", day: "numeric" };

      this.tipoServicio = item.tipoServicio;
      this.fecha = fecha.toLocaleDateString("es-ES", options);
      this.embarcacion = item.embarcacion;
      this.listarDetalles(item._id);

      this.comprobanteModal = 1;
    },
    ocultarComprobante(item) {
      this.comprobanteModal = 0;
    },
    ocultarComprobantePasajero(item) {
      this.comprobanteModalPasajero = 0;
    },

    printDiv() {
      var objeto = document.getElementById("solicitudGuardia"); //obtenemos el objeto a imprimir
      var ventana = window.open("", "_blank"); //abrimos una ventana vacía nueva
      ventana.document.write(objeto.innerHTML); //imprimimos el HTML del objeto en la nueva ventana
      ventana.document.close(); //cerramos el documento
      ventana.print(); //imprimimos la ventana
      ventana.close(); //cerramos la ventana
    },

    printDivPasajeros() {
      var objeto = document.getElementById("solicitudGuardiaPasajeros"); //obtenemos el objeto a imprimir
      var ventana = window.open("", "_blank"); //abrimos una ventana vacía nueva
      ventana.document.write(objeto.innerHTML); //imprimimos el HTML del objeto en la nueva ventana
      ventana.document.close(); //cerramos el documento
      ventana.print(); //imprimimos la ventana
      ventana.close(); //cerramos la ventana
    },

    mostrarComprobantePasajero(item) {
      this.limpiar();

      this.listarAgencia(item.usuario.agencia);
      this.folio = item.folio;
      var solic_fec = item.fecha;
      var fecha = new Date();
      this.operacion = item.operacion;
      var options = { year: "numeric", month: "long", day: "numeric" };
      var fec = new Date(solic_fec);
      this.fecha_solicitud = fec.toLocaleString();
      this.fecha = fecha.toLocaleDateString("es-ES", options);
      this.embarcacion = item.embarcacion;
      this.listarDetalles(item._id);
      this.comprobanteModalPasajero = 1;
    },
    selectEmbarcaciones() {
      let me = this;
      let embarcacionArray = [];
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("embarcacion/list", configuracion)
        .then((response) => {
          embarcacionArray = response.data;
          embarcacionArray.map(function (x) {
            me.embarcaciones.push({ text: x.nombre, value: x._id });
          });
        })
        .catch(function (error) {
          console.log(error);
        });
    },

    listarAgencia(id) {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("agencia/query?_id=" + id, configuracion)
        .then(function (response) {
          me.agenciasUsuario = response.data;
        })
        .catch(function (error) {
          console.log(error);
        });
    },

    selectAgencias() {
      let me = this;
      let agencaiArray = [];
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("agencia/list", configuracion)
        .then((response) => {
          agencaiArray = response.data;
          agencaiArray.map(function (x) {
            me.agencias.push({ text: x.nombre, value: x._id });
          });
        })
        .catch(function (error) {
          console.log(error);
        });
    },

    descargarCG(item) {
      window.open(item.adjunto, "_blank");
    },

    validar() {
      this.valida = 0;
      this.validaMensaje = [];
      if (!this.rol) {
        this.validaMensaje.push("El rol es obligatorio");
      }
      if (this.empresa.length < 1 || this.empresa.length > 30) {
        this.validaMensaje.push(
          "El nombre de la empresa debe tener entre 1 y 30 caracteres"
        );
      }
      if (this.nombre.length < 1 || this.nombre.length > 50) {
        this.validaMensaje.push(
          "El nombre del usuario debe tener entre 1 y 50 caracteres"
        );
      }
      if (this.email.length < 1 || this.email.length > 50) {
        this.validaMensaje.push("El email debe tener entre 1 y 50 caracteres");
      }
      if (this.password.length < 1 || this.password.length > 64) {
        this.validaMensaje.push(
          "El password debe tener entre 1 y 64 pasajeros"
        );
      }

      if (this.validaMensaje.length) {
        this.valida = 1;
      }
      return this.valida;
    },

    agregarDetalle() {
      this.pasajeros.push({
        nombreCompleto: "",
        identificacion: "",
        libretaMar: "",
        destino: "",
      });
    },
    limpiar() {
      this._id = "";
      (this.usuario = ""),
        (this.agencia = ""),
        (this.fecha = ""),
        (this.pasajeros = []),
        (this.valida = 0),
        (this.validaMensaje = []),
        (this.editedIndex = -1);
      this.verDetallee = 0;
    },
    guardar() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };

      if (this.editedIndex > -1) {
        axios
          .put(
            "cambioGuardia/update",
            {
              _id: this._id,
              embarcacion: this.embarcacion,
              usuario: this.$store.state.usuario._id,
              detalles: this.detalles,
              agencia: this.agencia,
              fecha: this.fecha,
              pasajeros: this.pasajeros,
            },
            configuracion
          )
          .then(function (response) {
            me.close();
            me.ocultarNuevo();
            me.listar();
          })
          .catch(function (error) {
            console.log(error);
          });
      } else {
        axios
          .post(
            "cambioGuardia/add",
            {
              embarcacion: this.embarcacion,
              usuario: this.$store.state.usuario._id,
              detalles: this.detalles,
              agencia: this.agencia,
              fecha: this.fecha,
              pasajeros: this.pasajeros,
              detalles: this.detalles,
            },
            configuracion
          )
          .then(function (response) {
            me.limpiar();
            me.close();
            me.listar();
          })
          .catch(function (error) {
            console.log(error);
          });
      }
    },
    listar() {
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      let me = this;
      axios
        .get("cambioGuardia/listAdmin", configuracion)
        .then(function (response) {
          me.cambiosGuardia = response.data;
        })
        .catch(function (error) {
          console.log(error);
        });
    },

    dialogo() {
      this.motivo_cancelacion = "";
      this.dialogs = true;
    },
    cerrarDialogo() {
      this.motivo_cancelacion = "";
      this.dialogs = false;
    },

    listarDetalles(id) {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .get("cambioGuardia/query?_id=" + id, configuracion)
        .then(function (response) {
          me.pasajeros = response.data.pasajeros;
        })
        .catch(function (error) {
          console.log(error);
        });
    },

    verGuardia(item) {
      this.limpiar();
      this.fecha = item.fecha;
      this.folio = item.folio;
      this.detalles = item.detalles;
      this.listarDetalles(item._id);
      this.verNuevo = 1;
      this.verDetallee = 1;
    },

    editItem(item) {
      this.limpiar();
      this.fecha = item.fecha;
      this.folio = item.folio;
      this.detalles = item.detalles;
      this.agencia = item.agencia._id;
      this.embarcacion = item.embarcacion._id;
      this.listarDetalles(item._id);
      this.verNuevo = 1;
      this._id = item._id;
      this.folio = item.folio;
      this.fecha = item.fecha;
      this.detalles = item.detalles;
      this.editedIndex = 1;
      this.dialog = false;
      this.verNuevo = 1;
    },

    activarDesactivarMostrar(accion, item, folio) {
      this.adModal = 1;
      this.adNombre = folio.folio;
      this.adId = folio;
      if (accion == 1) {
        this.adAccion = 1;
      } else if (accion == 2) {
        this.adAccion = 2;
      } else if (accion == 3) {
        this.adAccion = 3;
      } else {
        this.adMmodal = 0;
      }
    },

    aprobarApi(item) {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .put(
          "cambioGuardia/aprobarApi",
          { _id: this._id, aprobacionApi: this.$store.state.usuario.nombre },
          configuracion
        )
        .then(function (response) {
          me.listarEmbarcacion(
            response.data.fecha.join(" al "),
            response.data.embarcacion,
            response.data.muelle,
            response.data.detalles,
            response.data.operacion,
            response.data.usuario.nombre,
            response.data.usuario.email
          );
          me.comprobanteModal = 0;
          swal(
            "Solicitud Aprobada",
            "Solicitud de Embarque y Desembarque de Personal aprobada por ausencia de APITAB",
            "success"
          );
          (me.adModal = 0),
            (me.adAccion = 0),
            (me.adNombre = ""),
            (me.adId = ""),
            me.listar();
        })
        .catch(function (error) {
          console.log(error);
        });
    },

    activar() {
      let me = this;
      let header = { Token: this.$store.state.token };
      let configuracion = { headers: header };
      axios
        .put(
          "cambioGuardia/activate",
          { _id: this._id, aprobacionAmls: this.$store.state.usuario.nombre },
          configuracion
        )
        .then(function (response) {
          me.listarEmbarcacion(
            response.data.fecha.join(" al "),
            response.data.embarcacion,
            response.data.muelle,
            response.data.detalles,
            response.data.operacion,
            response.data.usuario.nombre
          );
          me.comprobanteModal = 0;
          swal(
            "Solicitud Aprobada",
            "Solicitud de Embarque y Desembarque de Personal aprobada con exito",
            "success"
          );
          (me.adModal = 0),
            (me.adAccion = 0),
            (me.adNombre = ""),
            (me.adId = ""),
            me.listar();
        })
        .catch(function (error) {
          console.log(error);
        });
    },

    desactivar() {
      let me = this;
      let header = { Token: this.$store.state.token };
      console.log(this.$store.state.usuario.nombre);
      let configuracion = { headers: header };
      axios
        .put(
          "cambioGuardia/deactivate",
          {
            _id: this._id,
            aprobacionAmls: this.$store.state.usuario.nombre,
            motivo_cancelacion: this.motivo_cancelacion,
          },
          configuracion
        )

        .then(function (response) {
          me.listarEmbarcacionCancelada(
            response.data.fecha.join(" al "),
            response.data.embarcacion,
            response.data.muelle,
            response.data.detalles,
            response.data.operacion,
            response.data.usuario.nombre,
            me.motivo_cancelacion,
            response.data.usuario.email
          );
          me.comprobanteModal = 0;
          swal(
            "Solicitud Cancelada",
            "Solicitud de Embarque y Desembarque de Personal Cancelada con exito",
            "success"
          );
          console.log(response.data.usuario.email);
          (me.adModal = 0),
            (me.adAccion = 0),
            (me.adNombre = ""),
            (me.adId = ""),
            me.listar();
        })
        .catch(function (error) {
          console.log(error);
        });
    },

    listarEmbarcacion(
      fecha,
      embar,
      muelle,
      detalles,
      operacion,
      usuario,
      correo
    ) {
      let header = { Token: this.$store.state.token };
      let me = this;
      let embarcacion;

      let configuracion = { headers: header };
      axios
        .get("embarcacion/query?_id=" + embar, configuracion)
        .then(function (response) {
          embarcacion = response.data;
          var fechaLocal;
          status = "aprobada";
          let cancelacion = "";

          me.enviar(
            fecha,
            embarcacion,
            muelle,
            detalles,
            operacion,
            usuario,
            status,
            cancelacion,
            correo
          );
        })
        .catch(function (error) {
          console.log(error);
        });
    },

    listarEmbarcacionCancelada(
      fecha,
      embar,
      muelle,
      detalles,
      operacion,
      usuario,
      cancelacion,
      correo
    ) {
      let header = { Token: this.$store.state.token };
      let me = this;
      let embarcacion;

      let configuracion = { headers: header };
      axios
        .get("embarcacion/query?_id=" + embar, configuracion)
        .then(function (response) {
          embarcacion = response.data;
          var fechaLocal;
          status = "cancelada";

          me.enviar(
            fecha,
            embarcacion,
            muelle,
            detalles,
            operacion,
            usuario,
            status,
            cancelacion,
            correo
          );
        })
        .catch(function (error) {
          console.log(error);
        });
    },

    activarDesactivarCerrar() {
      this.adModal = 0;
    },

    close() {
      this.dialog = false;
    },
  },
};
</script>

