      </template>
      <template v-slot:item.action="{ item }">

       <v-icon
        v-if="item.estado==0"
          small
          class="mr-2"
          @click="editItem(item)"
        >
          edit
        </v-icon>

      

    
       
        <v-icon
        v-if="item.estado==2"
          
          class="mr-2"
          @click="editItem(item)"
        >
          edit
        </v-icon>
       
          <v-icon
        v-if="item.estado==3"
          
          class="mr-2"
          @click="mostrarComprobante(item)"
        >
          text_snippet
        </v-icon>
       

      </template>
