                              <v-col cols="4">
                                  <v-subheader class="pa-0">Periodo de la estadia</v-subheader>
                              </v-col>
                              
                              <v-col cols="8" class="text--secondary">
                                 <v-dialog
                                    ref="dialog"
                                    v-model="modal"
                                    :return-value.sync="date"
                                    persistent
                                    width="290px"
                                  >
                              <template v-slot:activator="{ on, attrs }">
                                <v-text-field
