      enviar(
      fecha,
      embarcacion,
      muelle,
      detalles,
      usuario,
      status,
      correo
    ) {
      var template_params = {
        fecha: fecha,
        embarcacion: embarcacion,
        muelle: muelle,
        detalles: detalles,
        usuario: usuario,
        status: status,
        correo:correo
      };

      emailjs.send("gmail", "vehiculopersonal", template_params).then(
        function(response) {
          if (response.text === "OK") {
          }
          console.log(
            "SUCCESS. status=%d, text=%s",
            response.status,
            response.text
          );
        },
        function(err) {
          alert("Ocurrió un problema al enviar el correo");
          console.log("FAILED. error=", err);
        }
      );
    },

     listarEmbarcacion(fecha, embar, muelle, detalles, usuario,correo) {
      let header = { Token: this.$store.state.token };
      let me = this;
      let embarcacion;

      let configuracion = { headers: header };
      axios
        .get("embarcacion/query?_id=" + embar, configuracion)
        .then(function(response) {
          embarcacion = response.data;
          var fechaLocal;
          status = "en proceso";

          
          me.enviar(fecha, embarcacion, muelle, detalles, usuario, status,correo);
        })
        .catch(function(error) {
          console.log(error);
        });
    },

            selectCompanias(){
