            this.nombreArchivo='',
            this.nombreArchivo2='',
            this.nombreArchivo3='',
            this.valida=0,
            this.validaMensaje=[],
            this.editedIndex=-1;
          },

          handleFileUpload(e){
             

             
            },
             handleFileUploadPersonal(e){
           
              this.file3 = this.$refs.file3.files[0];
             
            },
             handleFileUploadManifiesto(e){
           
              this.file = this.$refs.file.files[0];
             
            },
            handleFileUploadVehiculos(e){
           
              this.file2 = this.$refs.file1.files[0];    
              console.log(this.file2)         
            },
            
          guardar(){
          
         
             let me=this;
             let header={"Token":this.$store.state.token};
             let configuracion= {headers: header}

           
              
             
            
              if(this.retiro_mat == '' && this.ingreso_mat==''){
                this.retiro_mat=false
                this.ingreso_mat=false
              }
              if(this.retiro_mat==''){
                this.retiro_mat=false
              }
              if(this.ingreso_mat==''){
                this.ingreso_mat=false
              }

             if(this.editedIndex >-1 ){    
               if(me.file){
                  let file=me.file
                  let fileExtension = file.type.split('/')[1]

                
                  this.nombreArchivo = `${me.url}.${fileExtension}`
               }
               if(me.file2){
                  let file2=me.file2
                  let fileExtension2 = file2.type.split('/')[1]

                
                  this.nombreArchivo2 = `${me.url2}.${fileExtension2}`
               }
               if(me.file3){
                  let file3=me.file3
                  let fileExtension3 = file3.type.split('/')[1]

                 

                  this.nombreArchivo3 = `${me.url3}.${fileExtension3}`
               }
               
                axios.put('vehiculoPersonal/actualizar',{
                  '_id':this._id,
                  'embarcacion':this.embarcacion,
                  'muelle':this.muelle,
                  'pasajeros':this.pasajeros,
                  'fecha':this.dates,
                  'vehiculos':this.vehiculos,
                   'manifiesto': this.nombreArchivo,
                   'vehiculos_adj':this.nombreArchivo2,
                   'personal_adj':this.nombreArchivo3,
                   'tipo':this.tipo,
                    'retiro_mat':this.retiro_mat,
                    'ingreso_mat':this.ingreso_mat,
                    'compania':this.compania,
                   'detalles':this.detalles
                   },configuracion)
               .then(function(response){
                  if(me.file){
                     me.S3Client
                    .uploadFile(me.file, me.newFileName)
                    
                  }
                  console.log(me.newFileName)
                   if(me.file2){
                     me.S3Client
                    .uploadFile(me.file2, me.newFileName2)
                  }
                    if(me.file3){
                     me.S3Client
                    .uploadFile(me.file3, me.newFileName3)
                  }
                  
                
                swal(
                  "Nueva Solicitud de Acceso Actualizada",
                  "Solicitud de acceso a vehiculos y personal actualizada exitosamente",
                  "success"
                )
                
                    
                 me.limpiar();
                  me.ocultarNuevo();
                 me.close();
                 me.listar();
               
               })
               .catch(function(error){
                  me.close(); 
                 swal(
                   "Error",
                   "Error al ingresar la solicitud, verifica los datos ingresados",
                   "error"
                 )
               });
             }else{
                
           
                  if(me.file){
                  let file=me.file
                  let fileExtension = file.type.split('/')[1]

                 
                  this.nombreArchivo = `${me.url}.${fileExtension}`
                }
               if(me.file2){
                  let file2=me.file2
                  let fileExtension2 = file2.type.split('/')[1]

                 
                  this.nombreArchivo2 = `${me.url2}.${fileExtension2}`
               }
               if(me.file3){
                  let file3=me.file3
                  let fileExtension3 = file3.type.split('/')[1]
                  
               
                  
                  this.nombreArchivo3 = `${me.url3}.${fileExtension3}`
               }
               this.submited = true
                this.$v.$touch();
                  if(this.$v.$invalid){
                    swal(
                      "Error",
                      "Verifique los datos",
                      "error"
                    )
                    return false
                  }
                axios.post('vehiculoPersonal/add',
                 {
                    
                    'embarcacion':this.embarcacion,
                    'usuario':this.$store.state.usuario._id,
                    'muelle':this.muelle,
                    'fecha':this.dates,
                    'pasajeros':this.pasajeros,
                    'vehiculos': this.vehiculos,
                    'manifiesto': this.nombreArchivo,
                    'manifiesto': this.nombreArchivo,
                    'vehiculos_adj':this.nombreArchivo2,
                    'personal_adj':this.nombreArchivo3,
                    'detalles':this.detalles,
                    'tipo':this.tipo,
                    'retiro_mat':this.retiro_mat,
                    'ingreso_mat':this.ingreso_mat,
                    'compania':this.compania
                    
               },configuracion)
               .then(function(response){
                   if(me.file){
                    
                     me.S3Client
                    .uploadFile(me.file, me.newFileName)
                  }
                   if(me.file2){
                     me.S3Client
                    .uploadFile(me.file2, me.newFileName2)
                  }
                    if(me.file3){
                     me.S3Client
                    .uploadFile(me.file3, me.newFileName3)
                  }
                   swal(
                  "Nueva Solicitud de Acceso Ingresada"+" Folio VP"+response.data.counter,
                  "Solicitud de acceso a vehiculos y personal ingresada exitosamente",
                  "success"
                )
                 me.listarEmbarcacion(
                response.data.fecha.join(' al '),
                response.data.embarcacion,
                response.data.muelle,
                response.data.detalles,
                me.$store.state.usuario.nombre,
                me.$store.state.usuario.email
              );
                 me.limpiar();
                me.ocultarNuevo();
                 me.close(); 
                 me.listar();
               
               }).catch(function(error){
                  me.close(); 
                 swal(
                   "Error",
                   "Error al ingresar la solicitud, verifique los datos ingresadoss",
                   "error"
                 )
               });
              }
          },

            descargarManifiesto(item){
                 window.open(item.manifiesto, '_blank');
          },
            descargarVehiculos(item){
                 window.open(item.vehiculos_adj, '_blank');
          },
            descargarPersonal(item){
                 window.open(item.personal_adj, '_blank');
          },
          listar(){
            let header={"Token":this.$store.state.token};
            let configuracion= {headers: header}
            let me=this;
            axios.get('vehiculoPersonal/list?valor='+this.$store.state.usuario._id,configuracion).then(function (response){
            me.cambiosGuardia=response.data;
            }).catch(function(error){
              console.log(error)
            })
          },
        
          listarDetalles(id){
            let me=this;
            let header={"Token":this.$store.state.token};
            let configuracion= {headers: header}
            axios.get('vehiculoPersonal/query?_id='+id,configuracion).then(function (response){
            me.pasajeros=response.data.pasajeros;
            }).catch(function(error){
              console.log(error)
            })
          },

          listarDetallesVehiculos(id){
            let me=this;
            let header={"Token":this.$store.state.token};
            let configuracion= {headers: header}
            axios.get('vehiculoPersonal/query?_id='+id,configuracion).then(function (response){
            me.vehiculos=response.data.vehiculos;
            }).catch(function(error){
              console.log(error)
            })
          },

          verGuardia(item){
            this.limpiar();
            this.fecha = item.fecha
            this.folio = item.folio
            this.muelle = item.muelle
            this.detalles = item.detalles
            this.agencia = item.agencia._id
            this.embarcacion = item.embarcacion._id
            this.listarDetalles(item._id);
            this.verNuevo=1;
          },

          editItem (item) {
            this.limpiar()
            this.listarAgencia(this.$store.state.usuario.agencia)
           
            this.muelle = item.muelle
            this.embarcacion = item.embarcacion._id
            this.listarDetalles(item._id);
            this.listarDetallesVehiculos(item._id)
            this.tipo=item.tipo
            this.compania=item.compania
            this.retiro_mat = item.retiro_mat
            this.ingreso_mat = item.ingreso_mat
           
            this.verNuevo=1;
            this._id=item._id;
            this.folio = item.folio
          
            this.dates = item.fecha
            this.nombreArchivo = item.manifiesto
            this.nombreArchivo2 = item.vehiculos_adj
            this.detalles = item.detalles
            this.editedIndex=1;
            this.dialog = false
            this.verNuevo=1
          },

            download(item){
               axios.get('vehiculoPersonal/getAccesoManifiesto/'+item.manifiesto, {responseType: 'arraybuffer'}).then(function (response){
               let blob = new Blob([response.data],{ type:'content-type'});
               let link = document.createElement('a');
               link.href = window.URL.createObjectURL(blob);
               link.download = "manifiesto.pdf";
               link.click();
            }).catch(function(error){
              console.log(error)
            })
          },

              downloadPersonal(item){
               axios.get('vehiculoPersonal/getAccesoPersonal/'+item.personal_adj, {responseType: 'arraybuffer'}).then(function (response){
               let blob = new Blob([response.data],{ type:'content-type'});
               let link = document.createElement('a');
               link.href = window.URL.createObjectURL(blob);
               link.download = "personal.pdf";
               link.click();
            }).catch(function(error){
              console.log(error)
            })
          },
             downloadVehiculo(item){
               axios.get('vehiculoPersonal/getAccesoVehiculo/'+item.vehiculos_adj, {responseType: 'arraybuffer'}).then(function (response){
               let blob = new Blob([response.data],{ type:'content-type'});
               let link = document.createElement('a');
               link.href = window.URL.createObjectURL(blob);
               link.download = "personal.pdf";
               link.click();
            }).catch(function(error){
              console.log(error)
            })
          },

        activarDesactivarMostrar(accion,item){
          this.adModal=1;
          this.adNombre=item.nombre;
          this.adId=item._id;
          if(accion==1){
            this.adAccion=1
          }else if(accion==2){
            this.adAccion=2
          }else{
            this.adMmodal=0;
          }
        },
        activar(){
          let me=this;
           let header={"Token":this.$store.state.token};
           let configuracion= {headers: header}
           axios.put('usuario/activate',{'_id':this.adId},configuracion)
               .then(function(response){
                 me.adModal=0,
                 me.adAccion=0,
                 me.adNombre='',
                 me.adId='',
                 me.listar();
               }).catch(function(error){
                 console.log(error)
               });
        },

        desactivar(){
          let me=this;
           let header={"Token":this.$store.state.token};
           let configuracion= {headers: header}
           axios.put('usuario/deactivate',{'_id':this.adId},configuracion)
               .then(function(response){
                 me.adModal=0,
                 me.adAccion=0,
                 me.adNombre='',
                 me.adId='',
                 me.listar();
               }).catch(function(error){
                 console.log(error)
               });
        },
        activarDesactivarCerrar(){
          this.adModal=0;
        },

          close () {
            this.dialog = false
          },

         
        }
  }
</script>

